import { T_StringOrNumber } from "shared/types/common";
import { isNullOrVoid } from "./dataStructure";

export function toTitleCase(str: string) {
  return str
    .replace(/-/g, " ")
    .replace(/\s+/g, " ")
    .replace(/[a-zA-Z]+/g, word => word[0].toUpperCase() + word.slice(1));
}

export function getLastPart(str: string, separator: string) {
  return str.split(separator).pop() ?? "";
}

export function pluralize(word: string, count: number) {
  return `${count} ${word}${count > 1 ? "s" : ""}`;
}

export function concat(
  strArray: (T_StringOrNumber | void | null)[],
  separator = ""
) {
  return strArray.filter((str) => !isNullOrVoid(str)).join(separator);
}

