import BreadCrumb from "shared/components/Breadcrumb";
import React from "react";
import { useNavigate } from "react-router-dom";

import useSiteSearch from "./hooks";
import "./styles.scss";

export default function HomeSearch() {
  const { inputValue, searchResults, handleInputValueChange } = useSiteSearch();
  const navigate = useNavigate();

  return (
    <div className="siteSearch">
      <div className="searchInputWrap">
        <input
          autoFocus
          type="text"
          className="searchInput"
          placeholder="search..."
          value={inputValue}
          onChange={handleInputValueChange}
        />
        <svg
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth="3"
            d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
          />
        </svg>
      </div>
      <div className="searchResultsWrap">
        {searchResults &&
          searchResults.map(({ item }) => {
            return (
              <div
                key={item.title}
                tabIndex={0}
                className="searchResultItemWrap"
                onClick={() => navigate(item.path)}
                onKeyPress={(event: React.KeyboardEvent) => {
                  if (event.key === "Enter") {
                    navigate(item.path);
                  }
                }}
              >
                <span className="font-bold tracking-wide">{item.title}</span>
                <BreadCrumb
                  path={item.path}
                  crumbSeparatorClassName="!w-4 !h-4"
                  crumbClassName="!text-xs !text-gray-400"
                  wrapperClassName="!px-0"
                  hideHome
                  makeCrumbsUnreachableViaKeyNav
                />
              </div>
            );
          })}
      </div>
    </div>
  );
}
