import { useCallback, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import appConstants from "shared/constants/app";
import { getCookie, deleteCookie } from "shared/helpers/cookie";
import { getScrollPosition, scrollToPosition } from "shared/helpers/window";
import { throttle } from "shared/helpers/optimization";


export function useVisitedPageCookieNavigation() {
  const navigate = useNavigate();
  const visitedPage = getCookie(appConstants.cookie.visitedPage);

  useEffect(() => {
    if (visitedPage) {
      deleteCookie(appConstants.cookie.visitedPage);
      // remove any trailing slash "/" and navigate
      navigate(visitedPage.replace(/\/?$/, ""));
    }
    // Run only once in App's lifetime
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return visitedPage;
}

export function useScrollToTop() {
  const [scrollPosition, setScrollPosition] = useState({ x: 0, y: 0 });
  const handleScroll = () => {
    const scrollPosition = getScrollPosition();
    setScrollPosition(scrollPosition);
  };
  const throttledHandleScroll = throttle(
    handleScroll,
    appConstants.scroll.eventThrottleInterval
  );
  const memoizedThrottledHandleScroll = useCallback(throttledHandleScroll, []);

  useEffect(() => {
    window.addEventListener("scroll", memoizedThrottledHandleScroll);

    return () => {
      window.removeEventListener("scroll", memoizedThrottledHandleScroll);
    };
  }, []);

  const hasSomeScroll =
    scrollPosition.y >= appConstants.scroll.minScrollForScrollToTop;

  return {
    hasSomeScroll,
    scrollToTop
  };
}

const scrollToTop = () => scrollToPosition({ y: 0, behavior: "auto" });
